import React, {useEffect, useState, useContext} from 'react'
import {Tabs, Spin} from 'antd'
import Loading from '../components/General/Loading'
import {Container} from '../components/General/Container'
import NavigationBar from '../components/Dashboard/NavigationBar'
import FormBusinessData from '../components/OnboardingFlow/Employers/FormBusinessData'
import ViewDashboard from '../components/Dashboard/Employer/ViewDashboard'
import {UserContext} from '../lib/UserProvider'
import {User} from '../model/model'
import {modifyUser} from '../lib/server'
const {TabPane} = Tabs

interface TabWithCeilingProps {
    tab: string;
    key: string;
    children: React.ReactNode
}

const DashboardEmployer: React.FC = (props) => {
    const {currentUser, changeUser}  = useContext(UserContext)

    const updateUser = async (updatedUser: User) => {
        changeUser(updatedUser)
        await modifyUser(updatedUser)
    }

    if (currentUser === null) {
        return (<Loading />)
    }

    return (
        <NavigationBar 
            title={`${currentUser.firstName} ${currentUser.lastName}'s Dashboard`} 
            subtitle="Modify Business Profile and Manage Communications" 
            footer={
                    <Tabs defaultActiveKey="1">
                        <TabPane tab={"View Communications"} key={"1"}>
                            <div style={{borderTop: '1px solid rgb(235, 237, 240)'}}>
                                <Container isDashboard={true}>
                                    <ViewDashboard/>
                                </Container>
                            </div>
                        </TabPane>
                        <TabPane tab={"Modify Profile"} key={"2"}>
                            <div style={{borderTop: '1px solid rgb(235, 237, 240)'}}>
                                <Container isDashboard={true}>
                                    <FormBusinessData changeCurrentUser={updateUser} modifyProfile={true} />
                                </Container>
                            </div>
                        </TabPane>
                    </Tabs>
            }
        />
    );
};

export default DashboardEmployer;